import React from 'react';
import Navbar from '../components/Navbar/Navbar.jsx'
import { Helmet } from 'react-helmet';
import Board from '../components/BlockBoard/BlockBoard.jsx'; // Ensure default export
import { useHttp } from '../hooks/useHttp.js'; // Import the custom hook

import "../styles/globals.css"

export default function Actions() {
  // Destructure values from the custom hook
  const { data, error, loading, httpRequestHandler } = useHttp(); 
  
  // Optional: If you want to handle loading and error states
  if (loading) {
    return <div>Loading...</div>;
  }

  if (error) {
    return <div>Error: {error.message}</div>;
  }

  return (
    <>
      <Helmet>
        <title>Actions Page</title>
        <meta name="description" content="Description of Actions page" />
      </Helmet>

      <Navbar elementActive="actions" />

      <main className='main md:overflow-hidden lg:overflow-hidden'>
        <Board httpRequestHandler={httpRequestHandler} />
      </main>
    </>
  );
}
