import React from "react";
import Navbar from "../components/Navbar/Navbar.jsx";

import { Toaster } from "../components/Toaster/Toaster.tsx"
import { toast } from "sonner";
import { Button } from "../components/Button/Button.tsx";


import "../styles/globals.css"
import {
  Card,
  CardContent,
  CardDescription,
  CardFooter,
  CardHeader,
  CardTitle,
} from "../components/Card/Card.tsx"
import { ScrollArea } from "../components/ScrollArea/ScrollArea.tsx";

import Divider from '@mui/material/Divider';
 
const tags = Array.from({ length: 50 }).map(
  (_, i, a) => `v1.2.0-beta.${a.length - i}`
)
 


async function CallBluetooth() {
    navigator.bluetooth.requestDevice({ acceptAllDevices:true})
.then(device => device.gatt?.connect())
.then(server => {
  // Getting Battery Service…
  return server?.getPrimaryService('battery_service');
})
.then(service => {
  // Getting Battery Level Characteristic…
  return service?.getCharacteristic('battery_level');
})
.then(characteristic => {
  // Reading Battery Level…
  return characteristic?.readValue();
})
.then(value => {
  console.log(`Battery percentage is ${value?.getUint8(0)}`);
})
.catch(error => { console.error(error); });
}
   

export default function Home() {
    


    return <>

    

        <Navbar elementActive="home"/>

        <Toaster expand={true} toastOptions={{
            unstyled: true,
            classNames: {
                toast: 'toast bg-special-hover rounded-lg',
                description: "description",
                title: 'title font-bold',
                icon: "icon",
                cancelButton: "cancel  hover:bg-primary/90 rounded-lg"


              },
              
              duration: 5000,
              
        }} visibleToasts={1} />

        <main className="">
            <h1>Homepage</h1>

            



            <p className=" text-2xl font-bold underline">Oi</p>

            <button onClick={CallBluetooth}>Clicka aqui</button>
           

           <div className="Connected-Device">
            <ScrollArea className="h-[60rem] w-[60rem] rounded-md border bg-red-600 overflow-y-visible">
              <div className="p-4 justify-center content-center m-auto">
              <h4 className="mb-4 text-sm font-medium leading-none">Tags</h4>
              {tags.map((tag) => (
                <>
                <div className="element-container  mb-10">
                  
                  
                  <p className="ml-[45%] text-2xl mt-10" key={tag}>{tag}</p></div>

                <Divider className="bg-white"/>
                </>
              ))}
              </div>
            </ScrollArea>
           </div>
            


        </main>

    </>
}