import React, { useEffect, useState, useRef } from 'react';
import { motion } from 'framer-motion';
import { useHttp } from '../../hooks/useHttp'; // Custom hook for HTTP requests
import { v4 as uuidv4 } from 'uuid';
import { Dialog, DialogContent, DialogDescription, DialogFooter, DialogHeader, DialogTitle, DialogTrigger } from '../Dialog/Dialog';
import { Select, SelectContent, SelectGroup, SelectItem, SelectLabel, SelectTrigger, SelectValue } from '../Select/Select';
import { Button } from '../Button/Button';
import { Label } from '../Label/Label';
import { Input } from '../Input/Input';
import { Flame, Plus, Trash, ChevronUp, Variable } from 'lucide-react';

import "../../styles/globals.css"

// Constants
const API_URL = 'http://localhost:5000/data';

// Utility function to get UUID from sessionStorage
const getGlobalStorageUuid = () => sessionStorage.getItem("GLOBAL_STORAGE_UUID");

// Function to store UUID in sessionStorage
const setGlobalStorageUuid = (uuid) => sessionStorage.setItem("GLOBAL_STORAGE_UUID", uuid);

const createPost = async (cards, httpRequestHandler) => {
    const payload = {
        Actions: {
            MovementSensor: cards
        }
    };

    let globalStorageUuid = getGlobalStorageUuid();

    try {
        if (!globalStorageUuid) {
            const [newUuidData] = await httpRequestHandler('get', API_URL);
            globalStorageUuid = newUuidData?._id;

            if (globalStorageUuid) {
                setGlobalStorageUuid(globalStorageUuid);
                await httpRequestHandler('put', `${API_URL}/${globalStorageUuid}`, payload);
            }
        } else {
            await httpRequestHandler('put', `${API_URL}/${globalStorageUuid}`, payload);
        }
    } catch (error) {
        console.error('Error creating or updating post:', error);
        try {
            const result = await httpRequestHandler('post', API_URL, payload);
            const [newUuidData] = await httpRequestHandler('get', API_URL);
            globalStorageUuid = newUuidData?._id;
            setGlobalStorageUuid(globalStorageUuid);
            console.log('Post created on retry:', result);
        } catch (retryError) {
            console.error('Error on retry:', retryError);
        }
    }
};

const loadData = async (httpRequestHandler) => {
    let globalStorageUuid = getGlobalStorageUuid();

    try {
        if (globalStorageUuid) {
            const result = await httpRequestHandler('get', `${API_URL}/${globalStorageUuid}`);
            return result;
        } else {
            const [newUuidData] = await httpRequestHandler('get', API_URL);
            globalStorageUuid = newUuidData?._id;
            if (globalStorageUuid) {
                setGlobalStorageUuid(globalStorageUuid);
                return await loadData(httpRequestHandler); // Recursively load data with the new UUID
            }
        }
    } catch (error) {
        console.error('Error retrieving data:', error);
    }
    return [];
};

const Board = () => {
    const [cards, setCards] = useState([]);
    const { httpRequestHandler } = useHttp();
    const [hasChecked, setHasChecked] = useState(false);
    const cooldownRef = useRef(false);

    useEffect(() => {
        const fetchData = async () => {
            const globalData = await loadData(httpRequestHandler);
            if (globalData?.Actions?.MovementSensor) {
                setCards(globalData.Actions.MovementSensor);
            }
            setHasChecked(true);
        };
        fetchData();
    }, [httpRequestHandler]);

    useEffect(() => {
        const saveData = async () => {
            if (!cooldownRef.current) {
                await createPost(cards, httpRequestHandler);
                cooldownRef.current = true;
                setTimeout(() => (cooldownRef.current = false), 2000); // 2-second cooldown
            }
        };

        if (cards.length > 0) saveData();
    }, [cards, httpRequestHandler]);

    return (
        <div className="board flex md:ml-[15rem] h-screen w-full gap-3 p-12">
            <Column title="Backlog" column="backlog" cards={cards} setCards={setCards} />
        </div>
    );
};

const Column = ({ title, headingColor = "text-neutral-500", column, cards, setCards }) => {
    const [active, setActive] = useState(false);

    const handleDragStart = (e, card) => {
        e.dataTransfer.setData('cardId', card.id);
    };

    const handleDragOver = (e) => {
        e.preventDefault();
        setActive(true);
    };

    const handleDragLeave = () => setActive(false);

    const handleDrop = (e) => {
        e.preventDefault();
        setActive(false);

        const cardId = e.dataTransfer.getData('cardId');
        const cardIndex = cards.findIndex((c) => c.id === cardId);
        if (cardIndex > -1) {
            const updatedCards = [...cards];
            const [cardToMove] = updatedCards.splice(cardIndex, 1);
            updatedCards.push({ ...cardToMove, column });
            setCards(updatedCards);
        }
    };

    const filteredCards = cards.filter((card) => card.column === column);

    return (
        <div className={`w-full sm:w-56 column rounded-md p-4 ${filteredCards.length < 1 ? "border-dashed" : "border-none"} border-2 border-gray-400`}>
            <div className="mb-3 flex items-center justify-between">
                <h3 className={`font-medium ${headingColor}`}>{title}</h3>
                <span className="rounded text-sm text-neutral-400">{filteredCards.length}</span>
            </div>
            <div className="h-full w-full overflow-y-scroll" onDragOver={handleDragOver} onDragLeave={handleDragLeave} onDrop={handleDrop}>
                {filteredCards.map((card) => (
                    <CardBlock key={card.id} card={card} handleDragStart={handleDragStart} cards={cards} setCards={setCards} />
                ))}
                <DropIndicator />
                <AddCard column={column} setCards={setCards} cards={cards} />
            </div>
        </div>
    );
};

const CardBlock = ({ card, handleDragStart, cards, setCards }) => {
    const [dialogOpen, setDialogOpen] = useState(false);
    const [compareValue, setCompareValue] = useState(card.value || "");
    const [compareOperation, setCompareOperation] = useState(card.operation || "equal");
    const [expressionEval, setExpressionEval] = useState(card.expressionEval || `X igual a ${card.value}`);

    useEffect(() => {
        const operationMap = {
            equal: "= (Igual a)",
            greater: "> (Maior que)",
            less: "< (Menor que)",
            greaterEqual: "≥ (Maior ou igual a)",
            lessEqual: "≤ (Menor ou igual a)"
        };
        setExpressionEval(`X ${operationMap[compareOperation]} ${compareValue}`);
    }, [compareValue, compareOperation]);

    const handleSubmit = () => {
        const updatedCards = cards.map((c) =>
            c.id === card.id ? { ...c, compareValue, compareOperation, expressionEval } : c
        );
        setCards(updatedCards);
        setDialogOpen(false);
    };

    return (
        <>
            <motion.div
                layoutId={card.id}
                draggable={true}
                onDragStart={(e) => handleDragStart(e, card)}
                className="cursor-grab rounded border border-neutral-700 bg-neutral-800 p-3 active:cursor-grabbing"
                onClick={() => setDialogOpen(true)}
            >
                <p className="text-sm text-neutral-100 text-center">{card.title}</p>
            </motion.div>
            <Dialog open={dialogOpen} onOpenChange={setDialogOpen}>
                <DialogContent>
                    <DialogHeader>
                        <DialogTitle>Edit Expression</DialogTitle>
                        <DialogDescription>
                            Make changes to the expression. Click Save when done.
                        </DialogDescription>
                    </DialogHeader>
                    <div className="grid gap-4 py-4">
                        <div className="grid grid-cols-4 items-center gap-4">
                            <Label htmlFor="operation">Operation</Label>
                            <Select value={compareOperation} onValueChange={setCompareOperation}>
                                <SelectTrigger className="w-[180px]">
                                    <SelectValue placeholder={expressionEval} />
                                </SelectTrigger>
                                <SelectContent>
                                    <SelectItem value="equal">= (Igual a)</SelectItem>
                                    <SelectItem value="greater">&gt; (Maior que)</SelectItem>
                                    <SelectItem value="less">&lt; (Menor que)</SelectItem>
                                    <SelectItem value="greaterEqual">≥ (Maior ou igual a)</SelectItem>
                                    <SelectItem value="lessEqual">≤ (Menor ou igual a)</SelectItem>
                                    <SelectItem value="lessEqual">&le; (Menor ou igual a)</SelectItem>
                                </SelectContent>
                            </Select>
                        </div>
                        <div className="grid grid-cols-4 items-center gap-4">
                            <Label htmlFor="value">Value</Label>
                            <Input
                                type="number"
                                value={compareValue}
                                onChange={(e) => setCompareValue(e.target.value)}
                                id="value"
                                placeholder="Enter value"
                                className="col-span-3"
                            />
                        </div>
                    </div>
                    <DialogFooter>
                        <Button variant="ghost" onClick={() => setDialogOpen(false)}>Cancel</Button>
                        <Button onClick={handleSubmit}>Save</Button>
                    </DialogFooter>
                </DialogContent>
            </Dialog>
        </>
    );
};

const DropIndicator = () => (
    <div className="h-16 border-dashed border-2 border-gray-300 rounded-md flex items-center justify-center opacity-50">
        <Plus className="text-gray-400" />
    </div>
);

const AddCard = ({ column, setCards, cards }) => {
    const [isAdding, setIsAdding] = useState(false);
    const [title, setTitle] = useState("");

    const handleAddCard = () => {
        const newCard = {
            id: uuidv4(),
            title,
            column,
            value: "",
            operation: "equal",
            expressionEval: `X igual a ${title}`
        };
        setCards([...cards, newCard]);
        setTitle("");
        setIsAdding(false);
    };

    return (
        <div className="mt-4">
            {isAdding ? (
                <div className="flex items-center">
                    <Input
                        type="text"
                        value={title}
                        onChange={(e) => setTitle(e.target.value)}
                        placeholder="Card title"
                        className="mr-2"
                    />
                    <Button onClick={handleAddCard}>Add</Button>
                    <Button variant="ghost" onClick={() => setIsAdding(false)}>Cancel</Button>
                </div>
            ) : (
                <Button onClick={() => setIsAdding(true)} className="w-full">
                    <Plus className="mr-2" /> Add Card
                </Button>
            )}
        </div>
    );
};

export default Board;
